import React, { Component } from "react";
import { Link, RichText, Date } from "prismic-reactjs";
import { bindActionCreators } from "redux";
import { fetchAllPrismic } from "../actions/prismicAction";
import { connect } from "react-redux";

class PostsList extends Component {
  componentDidMount() {
    this.props.fetchAllPrismic();
  }

  renderPosts() {
    console.log("le data", this.props.data);

    this.props.data &&
      Object.keys(this.props.data).map(key => {
        const posts = this.props.data[key];
        const data = this.props.data[key].data;
        console.log("data", posts.type);

        if (posts.type === "post") {
          return (
            <ul key={posts.id}>
              <li>{RichText.asText(data.title)}</li>
              <li>{RichText.asText(data.description)}</li>
              <img alt="cover" src={data.image.url} />
            </ul>
          );
        } else {
          return <p>loading...</p>;
        }
      });
  }

  render() {
    // console.log("fetch", this.props.posts);
    return (
      <div>
        <h3>header</h3>
        {this.renderPosts()}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    data: state.data
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({ fetchAllPrismic }, dispatch);
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PostsList);

